import Base from 'u-base.vue';
import Button from 'u-button.vue';

const Modal = Base.extend({
    name: 'u-modal',
    props: {
        title: { type: String, default: '' },
        okButton: { type: String, default: '' },
        cancelButton: { type: String, default: '' },
        content: { type: String, default: '' },
        visible: { type: Boolean, default: false },
        width: { type: [String, Number], default: '400' },
    },
    data() {
        return {
            visible_: this.visible,
        };
    },
    watch: {
        visible(visible) {
            this.visible_ = visible;
        },
    },
    methods: {
        open() {
            this.visible_ = true;
            if (!this.$el) {
                const ele = document.createElement('div');
                this.$mount(ele);
                document.body.appendChild(this.$el);
            }

            this.$emit('open');
        },
        close() {
            this.visible_ = false;
            this.$emit('close');
        },
        ok() {
            this.$emit('ok');
            this.close();
        },
        cancel() {
            this.$emit('cancel');
            this.close();
        },
    },
});

/**
 * @method alert(content[,title]) alert
 * @static
 * @public
 * @param  {string=''} content 
 * @param  {string=''} title 
 */
Modal.alert = (content, title = '', okButton = '') => {
    const modal = new Modal({
        data: { content, title, okButton, cancelButton: '' },
    });

    modal.open();
};

/**
 * @method confirm(content[,title]) confirm
 * @static
 * @public
 * @param  {string=''} content 
 * @param  {string=''} title 
 */
Modal.confirm = (content, title = '', okButton = '', cancelButton = '') => {
    const modal = new Modal({
        data: { content, title, okButton, cancelButton },
    });

    modal.open();
};

export default Modal;



